import React, { Suspense } from 'react'
import { useSelector } from 'react-redux'
import {
  Redirect,
  Route,
  Switch
} from 'react-router-dom'
import { CContainer, CFade } from '@coreui/react'

// routes config
import routes from '../routes'
import { roleChecking } from './services'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

const TheContent = () => {

  const userRole = useSelector(state => state.user?.role)
  const userModules = useSelector(state => state?.user?.modules)
  return (
    <main className="c-main py-2 pl-3 pr-1">
      <CContainer fluid>
        <Suspense fallback={loading}>
          <Switch>
            {routes?.filter(module => userModules?.includes(module?.moduleName) || (!module?.moduleName && userRole === 'admin'))?.filter(data => roleChecking(userRole, data)).map((route, idx) => {
              return route.component && (
                <Route
                  key={idx}
                  path={route.path}
                  exact={route.exact}
                  name={route.name}
                  render={props => (
                    <CFade>
                      <route.component {...props} />
                    </CFade>
                  )} />
              )
            })}
            <Redirect from="/" to="/landing-page" />
          </Switch>
        </Suspense>
      </CContainer>
    </main>
  )
}

export default React.memo(TheContent)
